import styles from './Screen2.module.css';
import {setActiveSlide} from "../../../store/reducers/Swiper";
import {useDispatch} from "react-redux";
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import {useEffect, useState} from "react";
import './data.css';
import TimeField from "react-simple-timefield";
import {dataStart, dataTime} from "../../../parsing/data";
import {ButtonFurther} from "../../../universal/ButtonFurther/ButtonFurther";
import {validateScreen_2} from "../validateSubmit/validateScreen_2";
import {setDataScreen_2} from "../../../store/reducers/Data";

export const Screen2 = () => {

  const dispatch = useDispatch();
  const [selectedDate, setSelectedDate] = useState(null);
  const [parsData, setParsData] = useState(dataStart());

  useEffect(() => {
    selectedDate && setParsData(dataTime(selectedDate))
  }, [selectedDate]);

  const [time, setTime] = useState("00:00");

  const handleChange = (event) => setTime(event.target.value);

  const onSetScreen_2 = () => {
    dispatch(setDataScreen_2({
      time,
      day: parsData,
    }));
    dispatch(setActiveSlide(2));
  };

  return (
    <div className={styles.container}>
      <p>Date {parsData}</p>
      <DatePicker
        selected={selectedDate}
        onChange={(date) => setSelectedDate(date)}
        style={{color: 'red', backgroundColor: 'lightgray', border: '1px solid black'}}
        inline={true}
        openToDate={new Date()}
      />
      <p>Time {time} o'clock</p>
      <TimeField
        value={time}
        onChange={handleChange}
        input={<input type="text" className={styles.TimeField}/>}
      />
      <br/>
      <div className={styles.bottomBtns}>
        <ButtonFurther
          title={'Back'}
          callback={() => dispatch(setActiveSlide(0))}
          className={styles.continue}
        />
        <ButtonFurther
          disabled={validateScreen_2(time)}
          title={'Continue ➤'}
          callback={() => onSetScreen_2()}
          className={styles.continue}
        />
      </div>
    </div>
  )
};
